<template>
    <div class="catalog">
        <ul>
            <li class="title item-wrapper">
                <span class="name"></span>
                <span class="state"></span>
                <span class="detail"></span>
            </li>
            <li v-for="(item, index) in list" :key="index" class="item-wrapper">
                <span class="name">{{item.name}}</span>
                <span class="state">
                    <el-checkbox v-model="obj[item.checked]"></el-checkbox>
                </span >
                <span @click="showDetail(item)" class="detail" v-html="item.checked !== 'hasClientInformation'&&item.checked !== 'has_client_information'? '':''">&nbsp;<i class="fa fa-angle-right" aria-hidden="true"></i></span>
            </li>
        </ul>
        <transition name="fade">
            <div class="detail-wrapper" v-show="detailShow">
                <v-header :title="currentItem.name" @goback="detailShow = false" next="" @submit="detailChange"></v-header>
                <rsList>
                    <ul slot="body">
                        <li class="checkbox">
                            <div class="text"></div>
                            <div class="check">
                                <el-radio v-model="obj[currentItem.radio]" :label=" false"></el-radio>
                                <el-radio v-model="obj[currentItem.radio]" :label=" true"></el-radio>
                                <div class="multi" v-for="(mutilcheck, index) in currentItem.options" :key="index">
                                    <el-checkbox v-model="obj[mutilcheck.key]" ></el-checkbox><span class="check-value">{{mutilcheck.value}}</span>
                                </div>
                            </div>
                        </li>
                        <li v-for="inputItem in currentItem.moreInput" :key="inputItem.model">
                            <rs-input @input="rsinput" 
                                :inputText="inputItem.label" 
                                :model="inputItem.model" 
                                :value="obj[inputItem.model]" 
                                :placeholder="inputItem.placeholder"
                            ></rs-input>
                        </li>
                        <li><rs-input @input="rsinput" inputText="" :model="currentItem.page" :value="obj[currentItem.page]"></rs-input></li>
                        <li><rs-input @input="rsinput" inputText="" :model="currentItem.area" :value="obj[currentItem.area]"></rs-input></li>
                    </ul>
                </rsList>
            </div>        
        </transition>
    </div>
</template>

<script type='text/ecmascript-6'>
import vHeader from 'components/header/header'
import rsList from 'base/rslist/rslist'
import rsInput from 'base/rsinput/rsinput'
  export default {
    props:{
        list:{
            type: Array,
            default: ()=>{
                return []
            }
        },
        obj:{
            type: Object,
            default: ()=>{
                return {}
            }
        }
    },
    data () {
      return {
          currentItem:{},
          detailShow:false
      }
    },
    components:{rsList, rsInput, vHeader},
    methods:{
        showDetail(item) {
            this.currentItem = item
            this.detailShow = true
        },
        rsinput(value, model) {
            this.obj[model] = value
        },
        detailChange() {
            if(this.obj[this.currentItem.radio] === null) {
                this.message = ''
                return
            }
            this.detailShow = false
        }
    }
  }
</script>

<style lang='stylus' scoped>
@import '~common/stylus/variable'
.catalog
    margin: 20px
    border 1px solid $color-border
    .title

        background $color-catalog-header 
    .item-wrapper
        display flex
        width 100%
        height 30px
        align-items center
        border-bottom 1px solid $color-border
        span
            display inline-block
            text-align center
            box-sizing border-box
            font-size $font-size-small
            &.name
                flex 1
                overflow: hidden
                text-overflow: ellipsis
                white-space: nowrap
            &.detail
                flex 0 0 100px
            &.state
                flex 0 0 60px
    .detail-wrapper
        position fixed
        top 0
        bottom 0
        left 0
        width 100%
        overflow auto   
        z-index 9
        background $pure-white
        .checkbox          
            // line-height 18px
            padding 15px 20px 15px 0
            margin-left: 20px
            display flex
            background $color-white
            font-size $font-size-medium
            border-bottom 1px solid  $color-border
            .text
                flex 0 0 60px
            .check 
                flex 1
        .multi
            border-top 1px solid $color-border
            line-height 18px
            margin-top 5px
            padding-top 5px
            .check-value
                padding-left: 10px
                
        


</style>


